hr {
    border-top-color: #74b49b;
    border-top-style: dotted;
    border-top-width: 7px;
    width: 8%;
    margin: 50px auto;
  }
  
.greeting {
    background-color: #f4f9f4;
    margin: 0;
    padding: 150px 0 60px;
    text-align: center;
}

#aboutMe {
    width: 40%;
    margin: auto;
}
.skills {
    background-color: #90c4af;
    width: 100%;
    margin: 0;
    padding: 75px 0 50px;
    text-align: center;
}

.skillsRow {
    width: 75%;
    margin: auto;
}

.textLight {
    color: #ddefe9;
}

.skillicon,
.skilliconS,
.skilliconL {
    height: 50px;
    float: left;
    margin-top: 30px;
    margin-left: 30px;
}

.skilliconS {
    height: 30px;
    margin-top: 40px;
}

.skilliconL {
    height: 60px;
}
.project-row {
    width: 75%;
    margin: 50px auto;
    text-align: left;
}

.project-image {
    width: 40%;
    float: left;
    margin-right: 30px;
}

.fa-github {
    vertical-align: middle;
    margin-left: 15px;
    color: #74b49b;
}

.fa-github:hover {
    color: #a7d7c5;
}
.individualProjects,
.groupProjects {
  background-color: #f4f9f4;
  margin: auto;
  padding: 50px 0;
  text-align: center;
}

#individualProj-carousel,
#groupProject-carousel {
  height: 375px;
}

.carousel-indicators li {
  background-color: #a7d7c5;
}

.carousel-indicators .active {
  background-color: #74b49b;
}

.caret-style {
  color: #74b49b;
  font-size: 2rem;
}
footer {
    line-height: 1.5;
    font-size: 1rem;
    text-align: center;
    color: #ddefe9;
    padding-top: 100px;
}

.footer {
    background-color: #74b49b;
    padding: 50px 0 5px;
    margin: 0 auto;
    text-align: center;
}

.buttons {
    margin-top: 30px;
}

.footer-links {
    margin: 20px 0 0;
}

.footer-link {
    color: #a7d7c5;
    margin: 0 10px;
    text-decoration: none;
}

.footer-link:hover {
    color: #f4f9f4;
}

.copyright {
    margin-bottom: 0;
}

.butn {
    background: #9DCAB9;
    background-image: linear-gradient(to bottom, #9DCAB9, #74b49b);
    border-radius: 8px;
    border: solid #68A28B 2px;
    font-family:'Philosopher', sans-serif;
    color: #ddefe9;
    font-size: 20px;
    padding: 10px 20px;
    margin-top: 20px;
    text-decoration: none;
  }
  
  .butn:hover {
    background: #74b49b;
    background-image: linear-gradient(to bottom, #74b49b, #9DCAB9);
    color: #ddefe9;
    text-decoration: none;
  }
html {
  position: relative;
  min-height: 100%;
}

.clearfix::after {
  content: "";
  clear: both;
  display: table;
}

body {
  font-family:'Philosopher', sans-serif;
  font-size: 20px;
  color: #8f8f8f;
  height: 100%;
  margin: 0 auto;
}

h1 {
  color: #74b49b;
  font-family: Ruthie, cursive;
  font-size: 5rem;
}

h2 {
  color: #74b49b;
  font-family: Ruthie, cursive;
  font-size: 3rem;
}

h3 {
  color: #74b49b;
  font-family:'Shadows Into Light Two', cursive;
  font-size: 1.5rem;
  font-weight: bold;
  margin: 10px auto;
}

h6 {
  font-size: 18px;
  text-align: left;
  vertical-align: middle;
  clear: both;
}

a {
  color: #74b49b;
  text-decoration: none;
}

a:hover {
  color: #a7d7c5;
  text-decoration: none;
}
